import React from 'react'

import Layout from '../components/layout'

import { makeStyles } from '@material-ui/core/styles'

import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'

import Button from '@material-ui/core/Button'

import TextField from '@material-ui/core/TextField'
import Autocomplete from '@material-ui/lab/Autocomplete'

import IconButton from '@material-ui/core/IconButton'
import DeleteIcon from '@material-ui/icons/Delete'

import Box from '@material-ui/core/Box'

import AddIcon from '@material-ui/icons/Add'

import InputAdornment from '@material-ui/core/InputAdornment';

import { useState } from "react";

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}))

const IndexPage = () => {

  //https://stackoverflow.com/questions/43230622/reactjs-how-to-delete-item-from-list

  //class App extends React.Component{


  const App = () => {
    const defaultList = [
      { name: "ItemOne" },
      { name: "ItemTwo" },
      { name: "ItemThree" }
    ];

    const [list, updateList] = useState(defaultList);

    const handleRemoveItem = (e) => {
      const name = e.target.getAttribute("name")
      updateList(list.filter(item => item.name !== name));
    };

    const handleAddItem = (e) => {
      updateList(list.concat({ name: "En ny!"}));
    };

    return (
      <div>
        {list.map(item => {
          return (
            <>
              <button name={item.name} onClick={handleRemoveItem}>
                Fjern
              </button>
              <div>{item.name}</div>
              <p>-----------------</p>
            </>
          );
        })}

      <button onClick={handleAddItem}>Tilf√∏j</button>
      </div>
    );
  };

  return (
    <App />
  )
}
export default IndexPage